* {
    box-sizing: border-box;
    font-family: belleza;
}

body {
    margin: 0;
    text-align: center;
}

h1 {
    font-family: berkshire;
}

button {
    font-size: 1.2em;
    color: white;
    background-color: $clr4;
    border-radius: 5px;
    border: none;
    cursor: pointer;

    &:hover:not(.back-btn) {
        color: $clr1;
    }
}

ul {
    list-style: none;
    padding-inline-start: 0;
    margin-block-start: 5px;
    margin-block-end: 5px;
}