.toy-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: $clr3;
    border: 3px solid $clr4;
    height: 280px;
    width: 246px;
    border-radius: 25px;


    h3,
    h4 {
        margin: 5px;
    }

    h2 {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 80%;
    }

    .img-container {
        width: 100%;
        background-color: white;
        height: 50%;
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;

        img {
            max-width: 100%;
            height: 100%;
            border-bottom-left-radius: 20px;
            border-bottom-right-radius: 20px;
        }
    }


    .btns-container {
        display: flex;
        // align-self: flex-start;
        margin-left: 5px;
        margin-top: 5px;
        visibility: hidden;

        img {
            width: 18px;
            height: 18px;
        }

    }

    &:hover .btns-container {
        visibility: unset;
    }

    button.user {
        width: 100%;
    }

    a:hover button {
        color: $clr4;
        background-color: $clr2;
    }

    button,
    a {
        background: none;
        border: none;
        width: 28px;
        height: 28px;
        font-size: 1.2em;
        margin: 0 3px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        color: $clr4;
        background-color: $clr1;
        font-size: 14px;
        font-weight: 600;
        cursor: pointer;

        &.user {
            width: fit-content;
            text-decoration: none;
            border-radius: 5px;
        }

        &:hover,
        &.user:hover {
            color: $clr4;
            background-color: $clr2;
        }
    }
}