.app-header {
    background-color: $clr4;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

header {
    .nav-line-container {
        background-color: $clr1;
        padding: 10px;
        width: 100%;
        box-shadow: 0px 0px 0px 5px #000000;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom-right-radius: 15px;
        border-bottom-left-radius: 15px;
        padding-left: 20px;
        padding-right: 20px;

        nav {
            height: 35px;
            padding: 0;
            display: flex;
            align-items: center;

            .active {
                text-decoration: underline;
            }
        }

        .modal {
            position: absolute;
            background-color: $clr4;
            border-radius: 5px;
            padding: 7px 7px;

            .modal-btn {
                background: $clr3;
                padding: 2px;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: flex-start;
                width: 100%;
                cursor: pointer;

                h5 {
                    display: inline;
                    margin: 0;
                    margin-left: 5px;
                    color: $clr4;
                    font-weight: 500;
                }

                &:hover h5 {
                    color: $clr2;
                }
            }

            img {
                display: inline;
                height: 22px;
                opacity: 50%;
            }
        }

        button {
            align-self: flex-start;
            height: 34px;
            width: 34px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0;
            padding: 0;
            border-radius: 50%;
            background-color: $clr2;

            &:hover {
                color: $clr4;
                opacity: 0.6;
            }
        }
    }

    a {
        text-decoration: none;

        &:not(.mister-toy-logo):not(.inf) {
            margin: 10px;
            color: brown;
        }

        &:not(.mister-toy-logo):not(.inf).active {
            font-weight: 400;
            // text-decoration: overline;
        }
    }

    .header-titles-container {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding-left: 20px;
        padding-right: 20px;
    }

    .header-inf {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;
        padding: 5px;
        padding-right: 0px;

        &>* {
            font-size: 0.8em;
        }
    }

    .mister-toy-logo,
    .inf {
        color: white;
        font-size: 10px;
        margin-top: 5px;
    }

    h1,
    h3,
    .inf {
        margin: 0 10px;
    }

    .inf:hover {
        color: $clr1;
    }

    h3 {
        color: $clr1;
        align-self: flex-end;
    }

    span {
        color: $clr1
    }

}




.nav-bar {
    background-color: black;
}